import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { jwtToken } from './authSignals';

const ProfileManagement = () => {
  const navigate = useNavigate();
  const [newEmail, setNewEmail] = useState('');
  const [currentEmail, setCurrentEmail] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const handleEmailChange = (event) => {
    setNewEmail(event.target.value);
  };

  useEffect(() => {
    fetch('/user/getEmail/?token=' + jwtToken.value)
      .then(response => response.json())
      .then(data => setCurrentEmail(data.email))
      .catch(error => console.error('Error fetching groups:', error));

  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const response = await fetch(`/user/changeEmail/?id=${jwtToken.value}&newEmail=${newEmail}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        } 
      });

      if (response.ok) {
        setCurrentEmail(newEmail);
        console.log('Shkpostiosoite vaihdettu onnistuneesti');
        setNewEmail(''); // Tyhjenn lomakekentt
      } else {
        const data = await response.json();
        setErrorMessage(data.error || 'Shkpostin vaihtaminen eponnistui');
      }
    } catch (error) {
      console.error('Virhe shkpostin vaihtamisessa:', error);
      setErrorMessage('Virhe shkpostin vaihtamisessa. Yrit uudelleen myhemmin.');
    }
  };

  const handleDeleteAccount = () => {
    // Nytetn vahvistusikkuna ja tallennetaan tulos
    const confirmDelete = window.confirm('Oletko varma ett haluat poistaa kyttjn?');

    // Jos kyttj vahvistaa poiston
    if (confirmDelete) {
      const token = sessionStorage.getItem('token');

      // Tehdn DELETE-pyynt kytten tokenia
      fetch('/user/?id=' + token, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        }
      })
        .then(response => {
          if (response.ok) {
            console.log('Kyttjtili poistettu onnistuneesti');
            // Tyhjennetn token sessionStoragesta
            sessionStorage.setItem('token', '');
            navigate('/login');
          } else {
            console.error('Kyttjtilin poistaminen eponnistui:', response.statusText);
          }
        })
        .catch(error => {
          console.error('Virhe kyttjtilin poistamisessa:', error);
        });
    }
  };

  return (
    <div className="profile-management">
      <div className="email-change-container">
        <h3>Vaihda shkposti</h3>
        <p>Nykyinen shkposti: <span className='currentEmail'>{currentEmail}</span></p>
        <form onSubmit={handleSubmit}>
          <input
            type="email"
            placeholder="Uusi shkpostiosoite"
            value={newEmail}
            onChange={handleEmailChange}
          />
          <button type="submit">Vaihda shkposti</button>
          {errorMessage && <p className="error-message">{errorMessage}</p>}
        </form>
      </div>
      <div className="account-container">
        <h3>Poista kyttjtili</h3>
        <button className="delete-account-button" onClick={handleDeleteAccount}>Poista kyttjtili</button>
      </div>
    </div>
  );
};

export default ProfileManagement;