import React, { useState, useEffect } from 'react';
import { jwtToken } from './authSignals';
import '../App.css';



export default function Groups() {

  function GroupList() {

    const requestGroupMembership = (group) => {
      console.log(group);
      const groupMemberStatusElement = document.getElementById('groupMemberStatus_'+group);
      groupMemberStatusElement.innerHTML = '';
      groupMemberStatusElement.innerHTML = '<span className="groupMembershipStatus">Liittymispyynt lhetetty</span>';
  
      const data = {
        token: jwtToken.value,
        group: group
      };
      console.log(data);
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-type': 'application/json' },
        body: JSON.stringify(data)
      };
  
      fetch('/groups/requestGroupMembership/', requestOptions)
        .then(response => response.json())
        .then(data => console.log(data))
        .catch(error => console.error('Error fetching groups:', error));
    }
  
    const [groups, setGroups] = useState([]);
    let token = '';
    if(jwtToken.value.length > 1){
      token = jwtToken.value;
    }
  
    useEffect(() => {
      fetch('/groups/All/' + token)
        .then(response => response.json())
        .then(data => setGroups(data))
        .catch(error => console.error('Error fetching groups:', error));
  
    }, []);
  
    return (
      <div className="groups-container"> {/* Lis luokka */}
  
        {groups.map(group => (
          <div className="group" key={group.idgroup}> {/* Lis luokka */}
            <div>
              <strong className="group-name">{group.name}</strong> {/* Lis luokka */}
            </div>
            <div className="group-description">{group.description}</div> {/* Lis luokka */}
            {jwtToken.value !== '' ? (
              <div id={`groupMemberStatus_${group.idgroup}`} className='groupMemberStatus'> 
              {group.ismember === '0' ? (
                <button className='button' id={`requestGroupMembership_${group.idgroup}`} onClick={() => requestGroupMembership(group.idgroup)}>Lhet liittymispyynt</button>
              ) : (
                group.isaccepted === true ? (
                  //<span className='groupMembershipStatus'>Olet ryhmn jsen</span>
                  <button className='button' id='' onClick={() => setView(group.idgroup)}>Avaa ryhmn sivu</button>
                ) : (
                  <span className='groupMembershipStatus'>Liittymispyynt lhetetty</span>
                )
              )}
              </div>
            ) : (
            <span> </span>
            )
            
            }
          </div>
        ))}
      </div>
    );
  }

function GroupPage(){
  const [isAdmin, setIsAdmin] = useState([]);
  const [user, setUser] = useState([]);
  const [groupId, setGroupId] = useState([]);
  const [groupName, setGroupName] = useState([]);
  const [groupDescription, setGroupDescription] = useState([]);
  const [groupMovieList, setGroupMovieList] = useState([]);
  const [groupShowList, setGroupShowList] = useState([]);
  const [groupMembers, setGroupMembers] = useState([]);

  useEffect(() => {
    fetch(`/groups/groupDetails/${view}/${jwtToken.value}`)
      .then(response => response.json())
      .then((data) => {
        setIsAdmin(data.admin);
        setUser(data.user);
        setGroupId(data.id);
        setGroupName(data.groupname);
        setGroupDescription(data.description);
        setGroupMovieList(data.groupMovieList);
        setGroupShowList(data.groupShowList);
        setGroupMembers(data.groupMembers);
      })
      .catch(error => console.error('Error fetching groups:', error));

  }, []);

  const removeUser = (user, group) => {

    const requestOptions = {
      method: 'delete',
      headers: { 'Content-type': 'application/json' }
    };
    console.log(requestOptions);
    fetch(`/groups/denyRequestToJoin/${user}/${group}`, requestOptions)
      .then(response => response.json())
      .then(data => {
        console.log(data.message);
        if(data.message == 'success'){
          console.log('Kyttj poistettu ryhmst');
          document.getElementById('groupMember_'+user).innerHTML = '<div class="acceptedToGroup">Hyltty</div>';
        }
      })
      .catch(error => {
        console.error(error);
      });
  };

  return(
    <div>
      <button className='button backToGroupsListButton' onClick={() => setView(0)}>Siirry takaisin ryhm-listaan</button>
      <div className='groupPageContainer'>
        <div className='groupPageGroupName' key={groupName}>{groupName}</div>
        <div className='groupPageGroupDescription' key={`description${groupId}`}>{groupDescription}</div>
        <div className='groupMovieListContainer'>
          <div className='groupMovieListHeading'>Ryhmn elokuvalista:</div>
          {groupMovieList.length == 0 ? (
            <div>Lista on tyhj</div>
          ) : (
            (groupMovieList.map((movie, i) =>
              <div className='groupMovieListItem' key={`movie${i}`}>
                <div className='groupMovieListMovie'>{movie.moviename}</div>
                <img src={movie.poster} alt={`Elokuvan ${movie.moviename} kuva`} />
                </div>
            ))
          )}

        </div>
        <div className='groupShowListContainer'>
          <div className='groupShowListHeading'>Ryhmn nytslista:</div>
          {groupShowList.length == 0 ? (
            <div>Lista on tyhj</div>
          ) : (
            (groupShowList.map((show, i) =>
              <div className='groupShowListItem' key={`show${i}`}>
                <div className='groupShowListMovie'>{show.moviename}</div>
                <div className='groupShowListTheater'>{show.theater}</div>
                <div className='groupShowListShowtime'>{show.showtime}</div>
                <img src={show.poster} alt={`Elokuvan ${show.moviename} kuva`} />
              </div>
            ))
          )}
        </div>
        <div className='groupMembersContainer'>
        <div className='groupMemberListHeading'>Ryhmn jsenet:</div>
          {groupMembers.map((member) => (
            <div className='groupMemberListItem' key={member.username} id={`groupMember_${member.id}`}> {member.username}
            {(isAdmin == 1 && user != member.id) && <button className='button removeFromGroup' onClick={() => removeUser(member.id, groupId)}>Poista jsen</button>}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

const [view, setView] = useState([0]);

  return(
    <div>
    {view < 1 ? (
      <GroupList />
    ) : (
      <GroupPage />
    ) }
    </div>
  );
  
}